[ng-drag] {
  -moz-user-select: -moz-none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

[ng-drag] {
  cursor: move;
}

[ng-drag].drag-over {
  border: solid 1px red;
}

[ng-drag].dragging {
  opacity: 0.5;
}

[ng-drop] {
  margin: 20px auto;
  position: relative;
}

[ng-drop].drag-enter {
  border: solid 5px red;
}

[ng-drop] span.title {
  display: block;
  position: absolute;
}

[ng-drop] div {
  position: relative;
  z-index: 2;
}

prello-board md-input-container {
  width: 100%;
}

prello-board md-input-container .md-errors-spacer {
  min-height: 0;
}

